*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
nav, ul {
    list-style: none;
}
a{
    text-decoration: none;
    cursor: pointer;
}
img{
    width: 400px;
}
p{
    text-align: justify;
    margin-bottom: 10px;
}




.navigation-container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 50px;
    border-bottom: 1px solid black;
}
.navigation-container-buttons{
    display: flex;
}
.navigation-container-buttons-button{
    margin: 0 10px;
}


.create-file{
    display: flex;
    align-items: center;
    justify-content: center;
}
.create-file-container{
    width: 1000px;
    background-color: rgb(230, 230, 230);
    padding: 20px;
}
.create-file-form{
    margin: 40px 10px;
    border-bottom: 2px solid rgb(155, 155, 155);
}
.create-file-form-textarea{
    width:938px;
    height:150px;
    margin-bottom: 20px;
    margin-top: 10px;
}
.create-file-button{
    width: 150px;
    height: 50px;
    margin: 0 400px;
}



.files-list{
    display: flex;
    align-items: center;
    justify-content: center;
}
.files-list-container{
    width: 1000px;
    background-color: rgb(230, 230, 230);
    padding: 20px;
}
.files-list-item{
    width: 940px;
    margin: 100px 10px;
    border-bottom: 2px solid rgb(155, 155, 155);
}
.files-list-item-texto{
    width: 940px;
}
.files-list-item-texto h4{
    margin-top: 30px;
    font-size: 20px;
}
.edit-button-container{
    margin: 40px 0;
}
.edit-button{
    padding: 10px 20px;
    background-color: rgb(207, 248, 216);
    border: 1px solid rgb(107, 107, 107);
    color: black;
    font-weight: bold;
    margin: 0 400px;
}
.delete-button-container{
    margin: 40px 0; 
}
.delete-button{
    padding: 10px 20px;
    background-color: rgb(248, 207, 207);
    border: 1px solid rgb(107, 107, 107);
    color: black;
    font-weight: bold;
    margin: 0 400px;
}

.index{
    display: flex;
    align-items: center;
    justify-content: center;
}
.index-container{
    width: 1000px;
    height: 800px;
    background-color: rgb(230, 230, 230);
    padding: 20px;
}